export const OPEN_MODAL = 'OPEN_MODAL';
export const CLOSE_MODAL = 'CLOSE_MODAL';
const LOAD_IMAGES = 'LOAD_IMAGES';

export function openModal(image) {
  return {
    type: OPEN_MODAL,
    image,
    modalOpen: true
  }
}

export function closeModal() {
  return {
    type: CLOSE_MODAL,
    modalOpen: false
  }
}

export function loadImages() {
  return {
    type: LOAD_IMAGES
  }
}



