import React, {Component} from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as GalleryActions from './../actions/actions.js'

import Modal from 'react-modal'
import FontAwesome from 'react-fontawesome'

import './Gallery.css'

export class Gallery extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.props.loadImages();
  }

  render() {
    const {images, selectedImage, openModal, closeModal, modalOpen, message} = this.props;
    return (
      <div className="gallery-container">
        { message ? <p>{message}</p> : null }
        <div className="gallery-grid">
          {images.map((image, index) => (
            <div key={index} className="gallery-card">
              <img className="gallery-thumbnail" src={image.thumbnail} onClick={() => openModal(image)}/>
            </div>
          ))}
        </div>
        <Modal
          isOpen={modalOpen}
          onRequestClose={() => closeModal()}
          contentLabel="Modal"
          // className="modal"
          // overlayClassName="modal-overlay"
          >
          { selectedImage ? <img src={selectedImage.large} /> : null}
          <FontAwesome className="close-button" name='close' onClick={() => closeModal()} />
        </Modal>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    images: state.images,
    selectedImage: state.selectedImage,
    modalOpen: state.modalOpen,
    error: state.error
  }
}

function mapActionCreatorsToProps(dispatch) {
  return bindActionCreators(GalleryActions, dispatch);
}

export default connect(mapStateToProps, mapActionCreatorsToProps)(Gallery);



