const defaultState = {
  images: []
}

export default function images(state = defaultState, action) {
  switch(action.type) {
    case 'OPEN_MODAL':
      return {...state, selectedImage: action.image, modalOpen: action.modalOpen};
    case 'CLOSE_MODAL':
      return {...state, modalOpen: action.modalOpen};
    case 'IMAGES_LOADED':
      return {...state, images: action.images}
    case 'IMAGE_LOAD_FAILURE':
      return {...state, error: action.error}
    default:
      return state;
  }
}



